<template>
  <v-container>
    <Spinner v-if="!track || !lyrics" />
    <v-card v-else class="mx-auto">
      <v-card-text>
        <div class="display-1">{{ track.artist_name }}</div>
        <p class="display-1 text--primary">
          {{ track.track_name }}
        </p>
        <div class="text--primary">
          {{ lyrics }}
        </div>
        <p class="mb-1 mt-3">
          <b class="text--primary">Album:</b> {{ track.album_name }}
        </p>
        <p class="mb-1">
          <b class="text--primary">Song Genre:</b>
          {{
            track.primary_genres.music_genre_list[0].music_genre
              .music_genre_name
          }}
        </p>
        <p class="mb-1">
          <b class="text--primary">Explicit Words:</b>
          {{ track.explicit === 0 ? 'No' : 'Yes' }}
        </p>
        <p class="mb-1">
          <b class="text--primary">Release Date:</b>
          {{ new Date(track.updated_time).toLocaleString() }}
        </p>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import Spinner from '@/components/Spinner'
export default {
  components: {
    Spinner,
  },
  computed: {
    track() {
      return this.$store.getters.track
    },
    lyrics() {
      return this.$store.getters.lyrics
    },
  },
  mounted() {
    this.$store.dispatch('fetchTrack', this.$router.history.current.params.id)
    this.$store.dispatch(
      'fetchTrackLyrics',
      this.$router.history.current.params.id
    )
  },
}
</script>
